/* define official THIS colours */
:root {
  --thisco-dark-grey: rgb(58 60 66 / 1);
  --thisco-light-grey: rgb(210 212 211 / 1);
  --thisco-red: rgb(221, 0, 49);
  --thisco-bright-blue: rgb(91, 194, 231);
  --thisco-pale-blue: rgb(143, 172, 217);
  --thisco-green: rgb(0, 187, 126);
  --thisco-pink: rgb(243, 165, 185);
  --thisco-aubergine: rgb(104 4 54 / 1);
  --thisco-yellow: rgb(255 216 90 / 1);
  --thisco-lighter-grey: #f4f4f4;
}

dialog {
  visibility: hidden;
}

.dialog-container,
.dialog-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.dialog-container {
  z-index: 2;
  display: flex;
  padding: 2rem;
  padding-top: clamp(100px, 2rem, 5rem);
}

.dialog-container[aria-hidden=true] {
  display: none;
}

.dialog-overlay {
  background-color: rgba(43, 46, 56, 0.9);
}

.dialog-content {
  margin: auto;
  z-index: 2;
  position: relative;
  background-color: white;
  border-radius: 25px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  max-width: 92%;
}
@media (min-width: 768px) {
  .dialog-content {
    max-width: 620px;
  }
}
@media (min-width: 980px) {
  .dialog-content {
    max-width: 820px;
  }
}
.dialog-content .dialog-header {
  padding: 1rem;
  border-bottom: 1px solid var(--thisco-light-grey);
  display: flex;
  justify-content: flex-end;
}
.dialog-content .dialog-body {
  padding: 1.25rem 1rem;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dialog-content .dialog-body {
    padding: 1.75rem 1.5rem;
  }
}
.dialog-content .dialog-body > * {
  margin-bottom: 0.6em;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
}
@keyframes slide-up {
  from {
    transform: translateY(10%);
  }
}
.dialog-overlay {
  animation: fade-in 150ms both;
}

/**
 * 1. Add an animation delay equal to the overlay animation duration to
 *    wait for the overlay to appear before animation in the dialog.
 */
.dialog-content {
  animation: fade-in 300ms 150ms both, slide-up 300ms 150ms both;
  /* 1 */
}

